import Navbar from '@/components/Navbar';
import {mapActions} from 'vuex';

export default{
components:{
  Navbar
},
methods:{
  ...mapActions(['setMobile']),
},
created() {
  if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
    this.setMobile(false);
  } else {
    this.setMobile(true);
  }
},

};

</script>

